import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type { 
  Calendar, 
  CalendarType, 
  CalendarStatus,
  CreateCalendarRequest,
  WorkingHours,
  TimeBlock
} from '@/lib/calendar/types'

// ==========================================
// GET /api/v1/calendars - List all calendars or get single calendar
// ==========================================
export async function GET(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const subAccountId = searchParams.get('subAccountId')
    const calendarId = searchParams.get('id')
    const calendarType = searchParams.get('type') as CalendarType | null
    const status = searchParams.get('status') as CalendarStatus | null
    const page = parseInt(searchParams.get('page') || '1')
    const limit = Math.min(parseInt(searchParams.get('limit') || '20'), 100)

    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    // If specific calendar requested
    if (calendarId) {
      const { data: calendar, error } = await supabase
        .from('calendars')
        .select(`
          *,
          calendar_working_hours (*),
          calendar_date_overrides (*),
          calendar_members (
            *,
            users:user_id (id, name, email, avatar_url)
          ),
          appointment_types (*)
        `)
        .eq('id', calendarId)
        .eq('sub_account_id', subAccountId)
        .single()

      if (error || !calendar) {
        return NextResponse.json(
          { success: false, error: 'Calendar not found' },
          { status: 404 }
        )
      }

      // Transform to API response format
      const transformedCalendar = transformCalendar(calendar)

      return NextResponse.json({
        success: true,
        data: transformedCalendar
      })
    }

    // List all calendars with filtering
    let query = supabase
      .from('calendars')
      .select('*', { count: 'exact' })
      .eq('sub_account_id', subAccountId)
      .order('created_at', { ascending: false })

    if (calendarType) {
      query = query.eq('calendar_type', calendarType)
    }

    if (status) {
      query = query.eq('status', status)
    } else {
      // Default: exclude archived
      query = query.neq('status', 'archived')
    }

    // Pagination
    const from = (page - 1) * limit
    const to = from + limit - 1
    query = query.range(from, to)

    const { data: calendars, error, count } = await query

    if (error) {
      console.error('Error fetching calendars:', error)
      // Check if table doesn't exist
      if (error.message?.includes('does not exist') || error.code === '42P01') {
        return NextResponse.json(
          { 
            success: false, 
            error: 'Calendar tables not found. Please run the database migration: supabase/migrations/015_calendar_appointment_system.sql',
            details: error.message
          },
          { status: 500 }
        )
      }
      return NextResponse.json(
        { success: false, error: 'Failed to fetch calendars', details: error.message },
        { status: 500 }
      )
    }

    const transformedCalendars = (calendars || []).map(transformCalendar)

    return NextResponse.json({
      success: true,
      data: transformedCalendars,
      pagination: {
        page,
        limit,
        total: count || 0,
        totalPages: Math.ceil((count || 0) / limit)
      }
    })

  } catch (error) {
    console.error('Error in GET /api/v1/calendars:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error', details: error instanceof Error ? error.message : 'Unknown error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/calendars - Create new calendar
// ==========================================
export async function POST(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      subAccountId,
      name,
      calendarType,
      timezone,
      description,
      durationMinutes = 30,
      slotIntervalMinutes = 15,
      bufferBeforeMinutes = 0,
      bufferAfterMinutes = 0,
      minimumNoticeHours = 0,
      maximumBookingDays = 60,
      autoConfirm = true,
      requiresApproval = false,
      maxCapacity,
      minAttendees,
      workingHours,
      customBranding,
      createdBy
    } = body as CreateCalendarRequest & { subAccountId: string; createdBy?: string }

    // Validation
    if (!subAccountId || !name || !calendarType || !timezone) {
      return NextResponse.json(
        { success: false, error: 'subAccountId, name, calendarType, and timezone are required' },
        { status: 400 }
      )
    }

    const validTypes: CalendarType[] = ['individual', 'team', 'collective', 'round_robin', 'service', 'class']
    if (!validTypes.includes(calendarType)) {
      return NextResponse.json(
        { success: false, error: `Invalid calendar type. Must be one of: ${validTypes.join(', ')}` },
        { status: 400 }
      )
    }

    // Generate unique slug
    const baseSlug = name
      .toLowerCase()
      .replace(/[^a-z0-9]+/g, '-')
      .replace(/^-|-$/g, '')
    const uniqueSlug = `${baseSlug}-${uuidv4().slice(0, 8)}`

    // Create calendar
    const calendarId = uuidv4()
    const { data: calendar, error: calendarError } = await supabase
      .from('calendars')
      .insert({
        id: calendarId,
        sub_account_id: subAccountId,
        name,
        slug: uniqueSlug,
        description,
        calendar_type: calendarType,
        status: 'active',
        timezone,
        duration_minutes: durationMinutes,
        slot_interval_minutes: slotIntervalMinutes,
        buffer_before_minutes: bufferBeforeMinutes,
        buffer_after_minutes: bufferAfterMinutes,
        minimum_notice_hours: minimumNoticeHours,
        maximum_booking_days: maximumBookingDays,
        auto_confirm: autoConfirm,
        requires_approval: requiresApproval,
        max_capacity: maxCapacity,
        min_attendees: minAttendees,
        custom_branding: customBranding,
        waitlist_enabled: false,
        allow_reschedule: true,
        allow_cancellation: true,
        created_by: createdBy
      })
      .select()
      .single()

    if (calendarError) {
      console.error('Error creating calendar:', calendarError)
      return NextResponse.json(
        { success: false, error: 'Failed to create calendar' },
        { status: 500 }
      )
    }

    // Create default working hours if not provided
    const defaultWorkingHours = workingHours || getDefaultWorkingHours()
    
    const workingHoursToInsert = defaultWorkingHours.map(wh => ({
      id: uuidv4(),
      calendar_id: calendarId,
      day_of_week: wh.dayOfWeek,
      is_available: wh.isAvailable,
      time_blocks: wh.timeBlocks
    }))

    const { error: whError } = await supabase
      .from('calendar_working_hours')
      .insert(workingHoursToInsert)

    if (whError) {
      console.error('Error creating working hours:', whError)
      // Don't fail the whole request, just log
    }

    // Generate booking URL
    const bookingUrl = `${process.env.NEXT_PUBLIC_APP_URL || ''}/book/${uniqueSlug}`

    return NextResponse.json({
      success: true,
      data: {
        ...transformCalendar(calendar),
        bookingUrl
      }
    }, { status: 201 })

  } catch (error) {
    console.error('Error in POST /api/v1/calendars:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT /api/v1/calendars - Update calendar
// ==========================================
export async function PUT(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      id,
      subAccountId,
      name,
      description,
      status,
      timezone,
      durationMinutes,
      slotIntervalMinutes,
      bufferBeforeMinutes,
      bufferAfterMinutes,
      minimumNoticeHours,
      maximumBookingDays,
      autoConfirm,
      requiresApproval,
      maxCapacity,
      minAttendees,
      waitlistEnabled,
      allowReschedule,
      rescheduleDeadlineHours,
      maxReschedules,
      allowCancellation,
      cancellationDeadlineHours,
      customBranding
    } = body

    if (!id || !subAccountId) {
      return NextResponse.json(
        { success: false, error: 'id and subAccountId are required' },
        { status: 400 }
      )
    }

    // Build update object
    const updates: Record<string, any> = {
      updated_at: new Date().toISOString()
    }

    if (name !== undefined) updates.name = name
    if (description !== undefined) updates.description = description
    if (status !== undefined) updates.status = status
    if (timezone !== undefined) updates.timezone = timezone
    if (durationMinutes !== undefined) updates.duration_minutes = durationMinutes
    if (slotIntervalMinutes !== undefined) updates.slot_interval_minutes = slotIntervalMinutes
    if (bufferBeforeMinutes !== undefined) updates.buffer_before_minutes = bufferBeforeMinutes
    if (bufferAfterMinutes !== undefined) updates.buffer_after_minutes = bufferAfterMinutes
    if (minimumNoticeHours !== undefined) updates.minimum_notice_hours = minimumNoticeHours
    if (maximumBookingDays !== undefined) updates.maximum_booking_days = maximumBookingDays
    if (autoConfirm !== undefined) updates.auto_confirm = autoConfirm
    if (requiresApproval !== undefined) updates.requires_approval = requiresApproval
    if (maxCapacity !== undefined) updates.max_capacity = maxCapacity
    if (minAttendees !== undefined) updates.min_attendees = minAttendees
    if (waitlistEnabled !== undefined) updates.waitlist_enabled = waitlistEnabled
    if (allowReschedule !== undefined) updates.allow_reschedule = allowReschedule
    if (rescheduleDeadlineHours !== undefined) updates.reschedule_deadline_hours = rescheduleDeadlineHours
    if (maxReschedules !== undefined) updates.max_reschedules = maxReschedules
    if (allowCancellation !== undefined) updates.allow_cancellation = allowCancellation
    if (cancellationDeadlineHours !== undefined) updates.cancellation_deadline_hours = cancellationDeadlineHours
    if (customBranding !== undefined) updates.custom_branding = customBranding

    const { data: calendar, error } = await supabase
      .from('calendars')
      .update(updates)
      .eq('id', id)
      .eq('sub_account_id', subAccountId)
      .select()
      .single()

    if (error || !calendar) {
      console.error('Error updating calendar:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to update calendar' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      data: transformCalendar(calendar)
    })

  } catch (error) {
    console.error('Error in PUT /api/v1/calendars:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE /api/v1/calendars - Archive calendar
// ==========================================
export async function DELETE(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const id = searchParams.get('id')
    const subAccountId = searchParams.get('subAccountId')
    const permanent = searchParams.get('permanent') === 'true'

    if (!id || !subAccountId) {
      return NextResponse.json(
        { success: false, error: 'id and subAccountId are required' },
        { status: 400 }
      )
    }

    if (permanent) {
      // Permanent delete
      const { error } = await supabase
        .from('calendars')
        .delete()
        .eq('id', id)
        .eq('sub_account_id', subAccountId)

      if (error) {
        console.error('Error deleting calendar:', error)
        return NextResponse.json(
          { success: false, error: 'Failed to delete calendar' },
          { status: 500 }
        )
      }
    } else {
      // Soft delete (archive)
      const { error } = await supabase
        .from('calendars')
        .update({ 
          status: 'archived',
          updated_at: new Date().toISOString()
        })
        .eq('id', id)
        .eq('sub_account_id', subAccountId)

      if (error) {
        console.error('Error archiving calendar:', error)
        return NextResponse.json(
          { success: false, error: 'Failed to archive calendar' },
          { status: 500 }
        )
      }
    }

    return NextResponse.json({ success: true }, { status: 200 })

  } catch (error) {
    console.error('Error in DELETE /api/v1/calendars:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// HELPER FUNCTIONS
// ==========================================

function transformCalendar(dbCalendar: any): Calendar {
  return {
    id: dbCalendar.id,
    subAccountId: dbCalendar.sub_account_id,
    name: dbCalendar.name,
    slug: dbCalendar.slug,
    description: dbCalendar.description,
    calendarType: dbCalendar.calendar_type,
    status: dbCalendar.status,
    timezone: dbCalendar.timezone,
    color: dbCalendar.color,
    avatarUrl: dbCalendar.avatar_url,
    customBranding: dbCalendar.custom_branding,
    durationMinutes: dbCalendar.duration_minutes,
    slotIntervalMinutes: dbCalendar.slot_interval_minutes,
    bufferBeforeMinutes: dbCalendar.buffer_before_minutes,
    bufferAfterMinutes: dbCalendar.buffer_after_minutes,
    minimumNoticeHours: dbCalendar.minimum_notice_hours,
    maximumBookingDays: dbCalendar.maximum_booking_days,
    autoConfirm: dbCalendar.auto_confirm,
    requiresApproval: dbCalendar.requires_approval,
    maxCapacity: dbCalendar.max_capacity,
    minAttendees: dbCalendar.min_attendees,
    waitlistEnabled: dbCalendar.waitlist_enabled,
    allowReschedule: dbCalendar.allow_reschedule,
    rescheduleDeadlineHours: dbCalendar.reschedule_deadline_hours,
    maxReschedules: dbCalendar.max_reschedules,
    allowCancellation: dbCalendar.allow_cancellation,
    cancellationDeadlineHours: dbCalendar.cancellation_deadline_hours,
    createdAt: dbCalendar.created_at,
    updatedAt: dbCalendar.updated_at,
    createdBy: dbCalendar.created_by,
    // Transform relationships if populated
    workingHours: dbCalendar.calendar_working_hours?.map((wh: any) => ({
      id: wh.id,
      calendarId: wh.calendar_id,
      dayOfWeek: wh.day_of_week,
      isAvailable: wh.is_available,
      timeBlocks: wh.time_blocks,
      createdAt: wh.created_at,
      updatedAt: wh.updated_at
    })),
    dateOverrides: dbCalendar.calendar_date_overrides?.map((d: any) => ({
      id: d.id,
      calendarId: d.calendar_id,
      overrideDate: d.override_date,
      overrideType: d.override_type,
      reason: d.reason,
      customHours: d.custom_hours,
      isRecurring: d.is_recurring,
      recurrencePattern: d.recurrence_pattern,
      createdAt: d.created_at
    })),
    members: dbCalendar.calendar_members?.map((m: any) => ({
      id: m.id,
      calendarId: m.calendar_id,
      userId: m.user_id,
      role: m.role,
      isPrimaryOwner: m.is_primary_owner,
      priorityOrder: m.priority_order,
      weight: m.weight,
      isActive: m.is_active,
      canEditCalendar: m.can_edit_calendar,
      canManageAppointments: m.can_manage_appointments,
      createdAt: m.created_at,
      user: m.users ? {
        id: m.users.id,
        name: m.users.name,
        email: m.users.email,
        avatar: m.users.avatar_url
      } : undefined
    })),
    appointmentTypes: dbCalendar.appointment_types?.map((at: any) => ({
      id: at.id,
      calendarId: at.calendar_id,
      name: at.name,
      description: at.description,
      slug: at.slug,
      durationMinutes: at.duration_minutes,
      bufferBeforeMinutes: at.buffer_before_minutes,
      bufferAfterMinutes: at.buffer_after_minutes,
      isActive: at.is_active,
      minimumNoticeHours: at.minimum_notice_hours,
      maximumBookingDays: at.maximum_booking_days,
      defaultLocationType: at.default_location_type,
      defaultLocation: at.default_location,
      videoProvider: at.video_provider,
      price: at.price,
      currency: at.currency,
      requirePayment: at.require_payment,
      depositAmount: at.deposit_amount,
      customQuestions: at.custom_questions,
      maxCapacity: at.max_capacity,
      color: at.color,
      icon: at.icon,
      displayOrder: at.display_order,
      createdAt: at.created_at,
      updatedAt: at.updated_at
    }))
  }
}

function getDefaultWorkingHours(): { dayOfWeek: number; isAvailable: boolean; timeBlocks: TimeBlock[] }[] {
  // Default: Monday-Friday 9am-5pm
  const defaultTimeBlocks = [{ startTime: '09:00', endTime: '17:00' }]
  
  return [
    { dayOfWeek: 0, isAvailable: false, timeBlocks: [] },           // Sunday
    { dayOfWeek: 1, isAvailable: true, timeBlocks: defaultTimeBlocks }, // Monday
    { dayOfWeek: 2, isAvailable: true, timeBlocks: defaultTimeBlocks }, // Tuesday
    { dayOfWeek: 3, isAvailable: true, timeBlocks: defaultTimeBlocks }, // Wednesday
    { dayOfWeek: 4, isAvailable: true, timeBlocks: defaultTimeBlocks }, // Thursday
    { dayOfWeek: 5, isAvailable: true, timeBlocks: defaultTimeBlocks }, // Friday
    { dayOfWeek: 6, isAvailable: false, timeBlocks: [] }            // Saturday
  ]
}
